body {
  padding: 20px;
  background-image: url('../images/main_background.jpg');
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.title {
  text-align: center;
  color: black;
}

input {
  display: inline-block;
  margin-left: auto;
  margin-right: auto;
}

.search-form {
  margin-bottom: 20px;
  text-align: center;
}

.search-form > .btn {
  display: inline-block;
}

.gallery {
  list-style: none;

  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 0;
  margin: auto;
}

.photo-card {
  max-width: 300px;

  margin-left: 10px;
  margin-right: 10px;
  margin-bottom: 10px;

  border-radius: 5px;

  color: white;
  background-color: teal;

  overflow: hidden;
}

.gallery__image {
  max-width: 100%;

  object-fit: cover;
  display: block;
  height: auto;

  cursor: pointer;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    transform: scale(1.2);
  }
}

.stats {
  display: flex;
  justify-content: space-between;
  padding: 15px;
}

.btn {
  display: block;
  margin-left: auto;
  margin-right: auto;
  padding: 5px 20px;

  border-radius: 5px;
  border: 0;

  background-color: tomato;
}

.btn.is-hidden {
  display: none;
}

.material-icons {
  display: flex;
  justify-content: center;
  font-size: 24px;
}
